import React, {Component} from "react";
import {OraButton} from "eshop/components/OraCommonComponents";
import OraModal from "eshop/modules/core/components/ui/Modal";

export default class OraCheckoutAssistModeConfirmationPopup extends Component {

    constructor(props) {
        super(props);
    };

    render() {
        return (
                <OraModal id={this.props.id} size={"narrow"} open={this.props.open}
                          showClose={false} escapeClose={false} clickClose={false}>
                    <div className="l-page-row u-font-bold" >
                        <div className="l-row">
                            <div className="l-col l-col-12">
                                {this.props.descriptions.messageText}
                            </div>
                        </div>
                        <div className="l-row u-spacing-top-l">
                            <div className="l-col l-col-small-6 l-col-medium-6 l-col-4 large-offset-by-4">
                                <OraButton className="u-w-100" type="secondary" onClick={this.props.onReject.bind(this)}>
                                    {this.props.descriptions.noButtonText}
                                </OraButton>
                            </div>
                            <div className="l-col l-col-small-6 l-col-medium-6 l-col-4 ">
                                <OraButton className="u-w-100" onClick={this.props.onConfirm.bind(this)}>
                                    {this.props.descriptions.yesButtonText}
                                </OraButton>
                            </div>
                        </div>
                    </div>
                </OraModal>
        )
    }
}

OraCheckoutAssistModeConfirmationPopup.defaultProps = {
    open: false,
    onConfirm: function(event) {},
    onReject: function(event) {}
};
